html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  color: #F9C802;
  background: #000;
  min-width: 100vw;
  min-height: 100vh;
  display: grid;
  grid-template-areas: "header header header" ". main ." ". footer .";
  grid-template-rows: 1fr 0.84fr 0.16fr;
  grid-template-columns: 1fr 3fr 1fr; }

.footer {
  grid-area: footer;
  background: hotpink;
  display: flex;
  justify-content: center;
  align-items: center; }

.header {
  grid-area: header;
  background: skyblue;
  min-height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center; }
  .header__logo {
    background-color: black;
    width: 100px;
    height: 100px;
    z-index: 5;
    color: hotpink; }
  .header__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: red; }

.main {
  grid-area: main;
  background: greenyellow;
  min-height: 84vh; }
